.About {
    background-color: white;
}

.wrapper-about {
    background-color: white;
    text-align: left;
    width: 100%;
    height: 100vh;
    max-width: 1080px;
    margin: 100px auto;
    align-items: center;
}

.wrapper-shrinked {
    width: 90%;
    height: 70%;
    max-width: 1040px;
    margin: 100px auto;
}

.grid-about {
    position: absolute;
    width: 100%;
    height: 100%;
    margin-top: 0;
    margin-bottom: 0;
}

.text-hello {
    font-family: Restart;
    font-style: regular;
    font-size: 28px;
    line-height: 150%;
    color: #b4b4b4;
    letter-spacing: -0.56px;
    margin-bottom: 40px;
}

.text-self-intro {
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    color: rgb(0, 0, 0);
    margin-bottom: 30px;
}

.text-intro-link {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: rgb(0, 0, 0);
    display: inline;
    margin-right: 40px;
}


.react-player-wrapper {
    display: flex;
    align-items: center;
    padding-top: 56.25%;
    /* Player ratio: 100 / (1280 / 720) */
    width: 90%;
    max-width: 1080px;
    margin: 0px auto;
}

.react-player {
    position: absolute;
    top: 0;
    left: 0;
}