@font-face {
  font-family: "Neue Hass Grotesk";
  src: url("../fonts/NeueHaasDisplay-Roman.ttf") format('opentype');
  font-weight: roman;
}

@font-face {
  font-family: "Restart";
  src: url("../fonts/RestartSoftDemo-Medium.otf") format('opentype');
}


@font-face {
  font-family: "Restart-r";
  src: url("../fonts/RestartSoftDemo-Regular.otf") format('opentype');
}

.Home {
  background-color: white;
}

.wrapper-1 {
  text-align: left;
  width: 100%;
  padding-top: 20px;
  align-items: center;
  display: block;
  background-color: #fff;
}


.link {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  color: #fff;
  mix-blend-mode: difference;
}

.text-name, .text-name-2 {
  font-family: Restart;
  font-style: regular;
  font-size: 22px;
  line-height: 150%;
  padding-top: 80px;
  letter-spacing: -1.08px;
  color: #b4b4b4
}

.text-name-2 {
  color: #494949
}

.text-name-2 {
  padding-top: 0px;
  font-size: 28px;
}

.gray {
  color: gray
}


.grid-container {
  padding-left: 3%;
  padding-right: 3%;
  padding-top: 0px;
  display: flex;
  width: 100%;
  margin-bottom: 70px;
}


.text-item-name, .text-item-name-medee {
  font-family: Restart;
  font-style: regular;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.48px;
  text-transform: capitalize;
  display: inline-block;
  position: relative;
  margin-bottom: 10px;
  color: #494949
}

.text-item-name::before, .text-item-name-medee::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background-color: #494949;
  transition: width 0.25s ease-out;
}

.hover::before {
  width: 100%;
}

.name-wrapper, [class^="title-image"], .title-medee, .static, .gif, .backgrad {
  width: 98%;
  margin: 0 auto;
}

[class^="title-image"], .title-medee, .static, .gif, .backgrad {
  display: block;
  border-radius: 12px;
}

.static, .gif, .backgrad {
  width: 100%;
}

.gif {
  display: none;
  z-index: 1;
}

.visible {
  display: inherit;
}

.invisible {
  display: none;
}

#aline, #ringle {
  position: relative;
  height: 69.39%;
}

.title-medee {
  position: relative;
  height: 69.39%;
  overflow: hidden;
  background-color: #4E77E3;
}

.title-background, .backgrad, .subtract {
  width: 100%;
  position: inherit;
}

.subtract {
  position: absolute;
  left: 0px;
  top: 0px;
  border-radius: 12px;
}

[class^="layer"] {
  position: absolute;
}

#athena {
  border: 1px solid rgb(198, 198, 198);
}

#l11, #l12, #l22 {
  top: 7.35%;
}

#l11, #l13, #l23 {
  left: 6.21%;
}

#l12, #l14, #l22, #l24 {
  right: 6.21%
}

#l13, #l14, #l23, #l24 {
  bottom: 9.54%;
}

#l11 {
  width: 22.29%
}

#l12 {
  width: 22.43%
}

#l13 {
  width: 22.56%
}

#l14 {
  width: 20.94%
}

#l22 {
  width: 19.19%
}

#l23 {
  width: 22.30%
}

#l24 {
  width: 38.78%
}

@keyframes l11 {
  0% {
    top: 13.3%
  }

  100% {
    top: 7.35%
  }
}

@keyframes l12 {
  0% {
    top: 13.3%
  }

  30% {
    top: 13.3%
  }

  100% {
    top: 7.35%
  }
}

@keyframes l22 {
  0% {
    top: 13.3%
  }

  40% {
    top: 13.3%
  }

  100% {
    top: 7.35%
  }
}

@keyframes l13 {
  0% {
    bottom: 3.54%;
  }

  50% {
    bottom: 3.54%;
  }

  100% {
    bottom: 9.54%;
  }
}

@keyframes l23 {
  0% {
    bottom: 3.54%;
  }

  62% {
    bottom: 3.54%;
  }

  100% {
    bottom: 9.54%;
  }
}

@keyframes l14 {
  0% {
    bottom: 3.54%;
  }

  65% {
    bottom: 3.54%;
  }

  100% {
    bottom: 9.54%;
  }
}

@keyframes l24 {
  0% {
    bottom: 3.54%;
  }

  69% {
    bottom: 3.54%;
  }

  100% {
    bottom: 9.54%;
  }
}

.l11 {
  animation: l11 0.49s ease-in-out forwards;
}

.l12 {
  animation: l12 0.56s ease-in-out forwards;
}

.l22 {
  animation: l22 0.6s ease-in-out forwards;
}

.l13 {
  animation: l13 0.65s ease-in-out forwards;
}

.l23 {
  animation: l23 0.77s ease-in-out forwards;
}

.l14 {
  animation: l14 0.88s ease-in-out forwards;
}

.l24 {
  animation: l24 1s ease-in-out forwards;
}


.medeeUI {
  position: absolute;
  left: 50%;
  top: 0px;
  width: 38.1%;
  transform: translate(-50%, 18.5%);
}

.bubble-1, .bubble-2, .bubble-3 {
  position: absolute;
  z-index: 1;
  left: 65.13%;
  top: 23.56%;
  width: 16.9%;
}

.bubble-2 {
  top: 34%;
  width: 28.1%;
}

.bubble-3 {
  left: 11.08%;
  top: 66%;
  width: 21.48%;
}

@keyframes bubble {
  0% {
    opacity: 0;
    top: 26.56%
  }

  15% {
    opacity: 0;
    top: 26.56%
  }

  75% {
    opacity: 1;
    top: 23.56%;
  }
}

@keyframes bubble-1s {
  0% {
    opacity: 0;
    top: 37%;
  }

  25% {
    opacity: 0;
    top: 37%;
  }

  55% {
    opacity: 1;
    top: 34%;
  }
}

@keyframes bubble-3s {
  0% {
    opacity: 0;
    top: 69%;
  }

  25% {
    opacity: 0;
    top: 69%;
  }

  35% {
    opacity: 1;
    top: 66%;
  }
}

.bubble-animation {
  animation: bubble 1s ease-in-out forwards;
}

.bubble-animation-1s {
  animation: bubble-1s 1.6s ease-in-out forwards;
}

.bubble-animation-3s {
  animation: bubble-3s 3.9s ease-in-out forwards;
}

@keyframes mouseover {
  0% {
    width: 98%;
  }

  75% {
    width: 100%;
  }

  100% {
    width: 99.9%;
  }
}

.hover-animation {
  animation: mouseover .5s ease-in-out forwards;
}

@keyframes vibrate {
  0% {
    transform: translate(-50%, 18.5%) rotate(0);
  }

  10% {
    transform: translate(-50%, 18.5%) rotate(1deg);
  }

  30% {
    transform: translate(-50%, 18.5%) rotate(-1deg);
  }

  50% {
    transform: translate(-50%, 18.5%) rotate(1deg);
  }

  65% {
    transform: translate(-50%, 18.5%) rotate(0deg);
  }
}

.vibrate {
  animation: vibrate .3s ease-in-out;
}



.text-item-name-sub {
  font-family: Restart-r;
  font-style: regular;
  font-size: 16px;
  line-height: 100%;
  display: inline;
  color: #838383
}


@media screen and (min-width: 600px) {

  .text-name {
    font-style: regular;
    font-size: 36px;
    line-height: 160%;
    padding-top: 150px;
  }

  .grid-container {
    padding-left: 3%;
    padding-right: 3%;
    padding-top: 0px;
    display: flex;
    width: 100%;
    margin-bottom: 150px;
  }

  .text-item-name, .text-item-name-medee {
    font-size: 20px;
    margin-bottom: 0px;

  }

  .text-item-name-sub {
    font-size: 20px;
  }


  .text-name-2 {
    padding-top: 0px;
  }

  [class^="title-image"], .title-medee {
    margin-top: 20px;
  }



  @keyframes mouseover {
    0% {
      width: 98%;
      margin-top: 20px
    }

    75% {
      width: 100%;
      margin-top: 10px
    }

    100% {
      width: 99.9%;
      margin-top: 10px
    }
  }

  .hover-animation {
    animation: mouseover .5s ease-in-out forwards;
  }

}








.link-item {
  text-decoration: none;
}




.hyper-links {
  font-family: Restart;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  color: #494949;
  margin-bottom: 20px;

}


.copy-project {
  font-family: Restart-r;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  color: #fff;
  mix-blend-mode: difference;
  margin-bottom: 40px;
  text-transform: uppercase;
}