.nav {
  position: fixed;
  width: 100%;
  top: 0px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  color: #ffffff;
  mix-blend-mode: difference;
  z-index: 9999 !important;
}

.nav a {
  text-decoration: none;
  font-size: 40px;
  font-weight: 300;
  text-align: center;
  color: #c7c7c7;
}


.topnav {
  display: flex;
  flex-direction: row;
  width: 100%
}

.grid-container-nav, .grid-container {
  padding-left: 3%;
  padding-right: 3%;
  display: flex;
  width: 100%;
}

.arrow {
  font-size: 24px;
  padding: 10px 0px 0px 20px;
}


.text-name-sooyeon {
  font-family: Restart;
  font-style: regular;
  font-size: 20px;
  line-height: 150%;
  display: inline-block;
  padding-bottom: 0.25rem;
  position: relative;
  color: #c7c7c7;
  flex-grow: 1;
  letter-spacing: -0.48px;
}

.taps a, .text-name-tap-underline, .text-name-tap {
  font-family: Restart;
  font-style: regular;
  font-size: 16px;
  padding-top: 0px;
  line-height: 150%;
  display: inline-block;
  padding-bottom: 0.25rem;
  position: relative;
  padding-right: 0px;
  letter-spacing: -0.48px;
}

.taps a {
  padding-top: 20px;
}

.text-name-tap-underline {
  padding-bottom: 0.6rem;
  text-decoration-line: underline;
  text-decoration-thickness: 0.1rem;
  padding-right: 20px;
}

.text-name-tap {
  padding-right: 20px;
}


@media screen and (min-width: 600px) {

  .grid-container-nav {
    padding-left: 3%;
    padding-right: 3%;
    padding-top: 20px;
    display: flex;
  }

  .text-name-sooyeon {
    font-size: 24px;
  }

  .text-name-tap {
    font-size: 20px;
    padding-right: 40px;
  }

  .taps a {
    font-size: 20px;
    padding-top: 12px;
  }

  .text-name-tap-underline {
    font-size: 20px;
    padding-bottom: 0.6rem;
    position: relative;
    padding-right: 40px;
  }

  .arrow {
    padding: 20px 0px 0px 30px;
  }
}


.taps {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  flex-grow: 9;
}