@-webkit-keyframes vibrate {
  0% {
    -webkit-transform: translate(-50%, 18.5%) rotate(0);
            transform: translate(-50%, 18.5%) rotate(0);
  }

  10% {
    -webkit-transform: translate(-50%, 18.5%) rotate(1deg);
            transform: translate(-50%, 18.5%) rotate(1deg);
  }

  30% {
    -webkit-transform: translate(-50%, 18.5%) rotate(-1deg);
            transform: translate(-50%, 18.5%) rotate(-1deg);
  }

  50% {
    -webkit-transform: translate(-50%, 18.5%) rotate(1deg);
            transform: translate(-50%, 18.5%) rotate(1deg);
  }

  65% {
    -webkit-transform: translate(-50%, 18.5%) rotate(0deg);
            transform: translate(-50%, 18.5%) rotate(0deg);
  }
}

@keyframes vibrate {
  0% {
    -webkit-transform: translate(-50%, 18.5%) rotate(0);
            transform: translate(-50%, 18.5%) rotate(0);
  }

  10% {
    -webkit-transform: translate(-50%, 18.5%) rotate(1deg);
            transform: translate(-50%, 18.5%) rotate(1deg);
  }

  30% {
    -webkit-transform: translate(-50%, 18.5%) rotate(-1deg);
            transform: translate(-50%, 18.5%) rotate(-1deg);
  }

  50% {
    -webkit-transform: translate(-50%, 18.5%) rotate(1deg);
            transform: translate(-50%, 18.5%) rotate(1deg);
  }

  65% {
    -webkit-transform: translate(-50%, 18.5%) rotate(0deg);
            transform: translate(-50%, 18.5%) rotate(0deg);
  }
}

