.player-wrapper {
    position: relative;
    padding-top: 56.25%;
    /* Player ratio: 100 / (1280 / 720) */
    width: 90%;
    max-width: 1200px;
    margin: 100px auto;
}

.react-player {
    position: absolute;
    top: 0;
    left: 0;
}

.background {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

